import React from "react";
import useLocalStorage from "react-use-localstorage";
import { QRCode } from "react-qr-svg";
import "./app.css";

const defaultItemsText = `
 
  
   
  	 	 
 	 	
	 	 	  	
	 	 	   
`.trim();

export function Item({ value }) {
  return (
    <li className="item">
      <QRCode value={value || ""} className="qrcode" />
      <h2>{value}</h2>
    </li>
  );
}
export function Items({ items, fontSize = "10mm" }) {
  return (
    <ul className="items" style={{ fontSize }}>
      {items.map((e, i) => (
        <Item value={e} key={e + i} />
      ))}
    </ul>
  );
}
export default function App() {
  const isBrowser = () => typeof window !== "undefined";
  return isBrowser() ? _App() : "";
}
function _App() {
  const [itemsText, setItemsText] = useLocalStorage(
    "itemsText",
    defaultItemsText
  );
  const [fontSize, setFontSize] = useLocalStorage("font-size", "10mm");
  const items = itemsText?.trim()?.split(/\s+/) || [];
  return (
    <div className="root">
      <div className="control">
        <div>
          <label>
            
            <input
              onChange={(e) => setFontSize(e.target.value)}
              value={fontSize}
            />
          </label>
        </div>
        <div>
          <label>
            
            <textarea
              onChange={(e) => setItemsText?.(e.target.value)}
              value={itemsText}
            />
          </label>
        </div>
      </div>
      <div className="print">
        <Items items={items} fontSize={fontSize} />
      </div>
    </div>
  );
}
